import test from 'ava';

import * as Client from './client.js';

const Window = require('window');

const window = new Window();

test.only.cb('afficher / masquer le texte', t => {
  t.plan(2);
  window.document.body.innerHTML = '<script src="home.js"></script><div id="seeMoreDiv" style="display:none">Des explications très intéressantes</div><button id="seeMoreButton" onclick="Client.seeMore()">Voir plus</button>';
  setTimeout(() => {
    window.document.getElementById('seeMoreButton').click();
    t.is(window.document.getElementById('seeMoreDiv').style.display, 'block');
    window.document.getElementById('seeMoreButton').click();
    t.is(window.document.getElementById('seeMoreDiv').style.display, 'none');
  }, 5000);
});
